<template>
<div id="imgList">
  <div  class="imgList" >
     <div v-for="(item,index) in imgList" :src="item" :key="index" class="list-item">
       <div class="imgBox"  @click="handleImgClick(item)">
          <img :src="item.picture" />
      </div>
      <p>{{item.prodName}}×{{item.amount}}</p>
     </div>
     
        <my-toast  :config="toastConfig" @cancel="toastConfig.dialogVisible=false">
          <el-form :model="it" v-for="(it,index) in  list" :key="index" ref="ruleForm" label-width="80px" >
            <el-row>
              <el-col :span="4">
                <el-form-item>
                  {{it.crafworkName}}
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item >
                  <div>
                    <img v-for="(item,index) in it.pictureUrls" :src="item" :key="index"  style="height:100px;max-width:100px;margin:0 15px;padding-right:20px;width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    height: 100px;
    margin: auto;
    margin: 0px 15px;
    padding-right: 20px;
" >
                  </div>
                </el-form-item>
              </el-col>
              <el-col :span="4" class="detailMessage">
                <!-- <p>
                  数量：{{it.amount}}
                </p> -->
                <p>
                  小计：{{it.price}}
                </p>
                <p>
                  完工时间：{{it.okDatetime}}
                </p>
              </el-col> 
            </el-row>  
        </el-form>
        <el-form :model="detailData" ref="ruleForm" label-width="80px" style="margin-top:20px;border-top:1px solid #e5e5e5;">
          <el-row>
            <el-col :span="4">
              <el-form-item label=""></el-form-item>
            </el-col>
            <el-col :span="3" style="margin-left:15px">
              <el-form-item label="">合计</el-form-item>
            </el-col>
            <!-- <el-col :span="6" style="margin-left:100px">
              <el-form-item label="合计数量:">{{detailData.sumAmount}}</el-form-item>
            </el-col> -->
            <el-col :span="8" offset="9">
              <el-form-item label="金额:">{{detailData.total}}</el-form-item>
            </el-col>
          </el-row>  
        </el-form>  
      </my-toast>
  </div>
  <div class="census">
    <el-form :model="form" ref="ruleForm" label-width="80px" >
      <el-row>
          <el-col :span="6">
            <el-form-item label="合计"></el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="合计数量:">{{form.sum}}</el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="金额:">{{form.sumTatal}}</el-form-item>
          </el-col>
        </el-row>        
      </el-form>
    </div>
  </div>
</template>

<script>
import myToast from "./myToast.vue";
// import HelloWorld from './components/HelloWorld'
import GetRequest from "./util.js";
export default {
  name: "App",
  data() {
    return {
      orderNo: "000231",
      imgList: [],
      form: {
        sumTatal: ""
      },
      detailData: {},
      list: [],
      toastConfig: {
        title: "产品明细", //弹窗标题
        dialogVisible: false, //弹窗显示
        width: "1200px", //弹窗宽
        btnData: [
          {
            isShow: true,
            disabled: false,
            name: "关闭页面", //按钮名
            method: "cancel" //回调函数
          }
        ]
      }
    };
  },
  methods: {
    handleImgClick(data) {
      this.$http.post(
        this.ports + "/remote/prodDelivery",
        {
          orderNo: data.orderNo,
          waresNo: data.waresNo,
          companyId: data.companyId
        },
        data => {
          console.log(data);
          this.list = [];
          data.pictMess.forEach(value => {
            this.list.push({
              crafworkName: value.crafworkName,
              orderNo: value.orderNo,
              waresNo: value.waresNo,
              amount: value.amount,
              pictureUrls: value.pictureUrls,
              unit: value.unit,
              price: value.price,
              sumUnit: value.sumUnit,
              okDatetime: value.okDatetime
            });
          });
          this.detailData = {
            sumAmount: data.sumAmount,
            total: data.total
          };
          this.toastConfig.dialogVisible = true;
        },
        this
      );
    }
  },
  components: {
    myToast
  },
  props: ["ports"],
  mounted() {
    console.log(GetRequest());
    this.orderNo = GetRequest().orderNo;
    var companyId = GetRequest().companyId;
    this.$http.post(
      this.ports + "/remote/orderDetail",
      { orderNo:this.orderNo,companyId:companyId },
      data => {
        console.log(data);
        this.imgList = data.taskFinishedMesDtos;
        this.form.sumTatal = data.sumTatal;
        this.form.sum = data.sum;
      },
      this
    );
  }
};
</script>

<style scoped>
body {
  padding: 0 20px;
}
#imgList {
  width: 1200px;
  margin: 0 auto;
}
.imgList::after {
  display: block;
  content: "";
  clear: both;
}
.list-item {
  float: left;
  text-align: center;
  width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#imgList .imgBox {
  width: 200px;
  height: 200px;
  text-align: center;
  display: flex;
  align-items: center;
  overflow: hidden;
  border: 1px #eee dashed;
  cursor: pointer;
}
#imgList .imgBox img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  height: 100px;
  margin: auto;
  display: block;
}
.census {
  border-top: 1px solid #e5e5e5;
  margin-top: 40px;
}
.el-form-item {
    margin-bottom: 0px;
}
</style>



